.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40px;
}

.App-header {
  background-color: #c964f4;
  height: 80px;
  padding: 10px;
  color: white;
}

.App-header h1 {
  margin: 0
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.Container {
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 auto;
  font-size: 40px;
}

.Container .item {
  width: 280px;
  padding: 10px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  float: left;
}

.Container .item .formular {
  float: left;
  width: 70%;
}

.Container .item .result {
  float: right;
  width: 30%;
}

.Container .item input {
  width: 100%;
  font-size: 40px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.result {
  position: relative;
}

.result input {
  text-align: center;
}

.tick {
  position: absolute;
  right: -35px;
  top: 0px;
  font-size: 34px;
}

.tick.ok {
  color: green;

}

.tick.error {
  color: red;
}

.button {
  margin-top: 30px;
  padding: 15px;
  font-size: 35px;
  margin-right: 30px;
  cursor: pointer;
}

.Container {
  padding-top: 35px;
}


